<template>
  <div class="goods_info">
  	<mt-swipe :show-indicators="false" :auto="3000" class="banner">
      <mt-swipe-item v-for="item in items">
        <div class="index_banner">
          <img :src="item"/>
        </div>
      </mt-swipe-item>
    </mt-swipe>

    <div class="infor">
    <div class="name">{{good_name}}</div>
    <div class="flex-item">
    	<div class="price">￥{{good_price}}</div>
    	<div class="sales">销量 {{xiaoliang}}</div>
    </div>
    </div>
    
    <div id="attr-box">
    	<div class="flex-item">
    		<div>
    			<span class="l-text">已选</span><span >正红色 1个</span>
    		</div>
    		<span>></span>
    	</div>
		<div class="flex-wrap" v-for="(item,key) in type_info">
			<div class="l-text">{{item.spec_name}}</div>
			<div>
				<span class="optionb" :class="item.idx === key && list.key === list.default ? 'option-active' : ''" v-for="(list,index) in item.value " @click="change(list,key, index)">{{list.value}}</span>
			
			</div>
		</div>
		<div class="flex-wrap quantity">
			<div class="l-text">数量</div>
			<div>
				<span class="minus" id="min" @click="cut">
					<!--<img src="img/img-icons/icon-min.png" style="width: 12px;"/>-->-
				</span>
				<input class="num" id="quantity" name="" type="text" v-model="num" disabled />
				<span class="plus" id="add" @click="add">
					<!--<img src="img/img-icons/icon-add.png" style="width: 12px;"/>-->+
				</span>
			</div>
		</div>
	</div>
	<div class="details">
		<div class="center-text">商品介绍</div>
		<div v-html='desc'></div>
		
	</div>
	
	<div class="fixed-bottom">
		<div>
			<!--<img src="../../assets/person/banner01.jpg" style="width: .4rem;height: .4rem;"/>-->
			<img src="../../assets/shops/icons/icon-cart.png" style="width: .4rem;height: .4rem;"/>
		</div>
		<div @click="addCar">加入购物车</div>
		<div @click="confirmorder">立即购买</div>
		
	</div>
  </div>
</template>
<script>
  import { Toast} from 'mint-ui';
  export default {
    data(){
      return {
        flag: false,
        goodsInfo: {},
        good_name:'',
        good_price:'',
        xiaoliang:'',
        good_desc:'',
        type_info:[],
        desc:'',
        items:[
//      	{src:'../../../dist/static/03.jpg'},
//      	{src:'../../../dist/static/03.jpg'}
        ],
        num:1,
        //商品id
        id: '',
        type:1,
        //、规格
   		spec:[]
      }
    },
    
    methods:{
    	//加
    	add(){
    		this.num ++;
    	},
    	cut(){
    		if(this.num > 1){
    			this.num --
    		}
    	},
    	change(item,key, index){
			this.type_info[key].value.forEach(list => {
				list.default = ''
			})
			item.default = index;
    	},
    	addCar(){
    		this.spec = [];
    		//又属性
    		if(this.type_info.length !== 0){
    			this.type_info.forEach(item =>{
    				var obj = {};
    				obj['name'] = item.spec_name;
    				obj['value'] = item.value.filter(list => {
    					return list.default !== ''
    				})[0].value;
    				this.spec.push(obj);
    			})
    		}else {
    			this.spec = [];
    		}
    		this.post('home/Cart/addShopToCart', {
    			good_id: this.id,
 				number: this.num,
		 		type_info: JSON.stringify(this.spec)
    		}).then(res => {
    			if(res.data.code === 10000){
    				this.$router.push('/shopCar')
    			}
    		})
    	},
    	confirmorder(){
    		this.$router.push('/confirmorder')
    	},
    	getShopById(good_id){
     	this.post('home/Category/getShopById', {good_id:good_id}).then(function(res) {
		if (res.data.code === 10000) {
			this.id= res.data.info.good_id;
            this.good_name = res.data.info.good_name;
            this.good_price = res.data.info.good_price;
            this.xiaoliang = res.data.info.xiaoliang;
            this.good_desc = res.data.info.good_desc;
            this.items=res.data.info.pic_url.map((function(item){
            return	item=this.imageURL(item)
            }.bind(this)));
            
            this.desc=res.data.info.good_desc;
            this.type_info=res.data.info.type_info;
            this.type_info.forEach((item,key) => {
            	item.idx = key;
            	item.value  = item.value.split(',');
            	var arr  = [];
            	item.value.forEach((item,key) => {
            		var obj = {};
            		obj['value'] = item;
            		obj.key = key;
            		obj.default = ''
            		arr.push(obj);
            	})
     			item.value = arr;
     			item.value[0]['default'] = 0
            })
          }
        }.bind(this))
     }
    },
    mounted(){

     this.getShopById(this.$route.params.id)
    }
  }
</script>
<style scoped>
  @import '../../../static/header.css';
  .banner{height: 7rem;width: 100%;}
  .index_banner img{height: 7rem;width: 100%;}
  
  .infor{background: #fff;padding: .2rem;}
  .flex-item{display: flex;flex-direction: row;justify-content: space-between;align-items: center;}
  .goods_info {
    height: calc(100vh - 0.9rem);
    overflow-y: auto;
  }
  .goods-img{display: block;width: 100%;}
  
  
#attr-box {background: #fff;padding:0 .2rem .2rem;border: 1px rgba(0,0,0,0) solid;margin: .2rem 0;}
#attr-box .flex-wrap{display: flex;margin-top: 10px;flex-direction: row;align-items: center;}

.l-text{height: 36px;line-height: 36px;color: #A2A3A3;margin-right: .4rem;font-size: .26rem;}
.optiona,.optionb{width:1rem;height:36px;line-height:36px;text-align:center;border-radius: 6px;background: #F2F2F4;
display: inline-block;margin-right: 10px;}

.option-active{background: #FF5773;color: #fff;}
.quantity span,.quantity input{width: 36px;text-align: center;background: #F2F2F4;border-radius: 3px;display: inline-block;height: .6rem;line-height: .6rem;}
.quantity input{width: 70px;border: 0;outline: none;}

.details{padding: .2rem;background: #fff; padding-bottom: 1rem;}
.details .center-text{text-align: center;padding: .2rem;background: #fff;}
.fixed-bottom{position: fixed;bottom:0;max-width: 750px;margin: auto;color: #fff;
 width: 100%;height: 1rem;line-height: 1rem; background:#fff ;text-align: center;display: flex;align-items: center;}
 
 .fixed-bottom div{
 	flex: 1;
 }
 .fixed-bottom div:nth-of-type(2){background: #FF798F;}
 .fixed-bottom div:nth-of-type(3){background: #FF5773;}
</style>



